@import '@/assets/sass/base/_reset.scss';
$TAB_BORDER_SIZE: 2px;
$TAB_HEIGHT: 40px;

.tab-nav-list {
  border-bottom: $TAB_BORDER_SIZE solid $tab-border;
}
.gt-tab-nav {
  font-size: 15px;
  color: $tab-font;
  background: $tab-bg;
  height: $TAB_HEIGHT;
  line-height: $TAB_HEIGHT - 2px;
  min-width: 110px;
  width: 110px;
  outline: none;
  position: relative;
  top: $TAB_BORDER_SIZE;
  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: $TAB_BORDER_SIZE;
    background-color: $tab-border;
  }
  &.active:after {
    background-color: $tab-border-active;
  }
  &:hover {
    color: $tab-font-active;
  }
}

