
          @import "@/assets/sass/base/_colors.scss";
          @import "@/assets/sass/mixins/_clearfix.scss";
          @import "@/assets/sass/mixins/_truncate.scss";
          @import url(https://static.gabia.com/fonts/icons/v1.0.1/style.css);
          




















































































.gt-modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  margin: 0;
  text-align: center;

  &:before {
    display: inline-block;
    content: "";
    height: 100%;
    vertical-align: middle;
    width: 0;
  }
}
.gt-modal {
  display: inline-block;
  vertical-align: middle;
  margin: 5vh auto;
  padding: 0 1rem;
  border: 1px solid $modal-border;
  background: #fff;
  color: $modal-font;
  text-align: left;
}
.gt-modal-head {
  padding: 1rem 0;
  border-bottom: 1px solid $modal-border;
}
.gt-modal-body {
  padding: 1rem 0;
}
.gt-modal-foot {
  text-align: center;
  padding-bottom: 1em;
  .gt-btn + .gt-btn {
    margin-left: 10px;
  }
}
