
          @import "@/assets/sass/base/_colors.scss";
          @import "@/assets/sass/mixins/_clearfix.scss";
          @import "@/assets/sass/mixins/_truncate.scss";
          @import url(https://static.gabia.com/fonts/icons/v1.0.1/style.css);
          














































































.gt-collapse-item {
  color: $collapse-font;
  line-height: 1.5;
  border-top: 1px solid $collapse-border;
  border-bottom: 1px solid $collapse-border;
  & + .gt-collapse-item {
    border-top: 0;
  }

  &.active > .gt-collapse-head {
    background-color: $collapse-head-bg-active;
  }
}

.gt-collapse-head,
.gt-collapse-body {
  padding: 1em;
}

.gt-collapse-head {
  background-color: $collapse-head-bg;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;

  .title {
    flex: 1 1 0;
    -ms-flex: 1 1 auto;
    min-width: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    order: 1;
    padding-right: 10px;
    &.reverse {
      order: 2;
      padding-left: 10px;
    }
  }
  .arrow {
    order: 2;
    &.reverse {
      order: 1;
    }
  }
}

.gt-collapse-body {
  border-top: 0;
}
