
          @import "@/assets/sass/base/_colors.scss";
          @import "@/assets/sass/mixins/_clearfix.scss";
          @import "@/assets/sass/mixins/_truncate.scss";
          @import url(https://static.gabia.com/fonts/icons/v1.0.1/style.css);
          





































































































$primary-color: #2589db;
label {
  display: inline-block;
  .hw-checkbox {
    position: relative;
    display: inline-block;
    display: block;
    font-size: 14px;
    padding: 0px;
    cursor: pointer;
    user-select: none;
    .checkbox-input {
      position: relative;
      display: inline-block;
      box-sizing: border-box;
      width: 16px;
      height: 16px;
      border: 1px solid #707070;
      background-color: #fff;
      vertical-align: middle;
    }
    .label {
      display: inline-block;
      vertical-align: middle;
      width: calc(100% - 21px);
      white-space: nowrap;
      margin-left: 5px;
      color: #333;
    }
    &.checked {
      .checkbox-input {
        background-color: $primary-color;
        border-color: $primary-color;
        &:after {
          position: absolute;
          content: "";
          border: 2px solid #fff;
          border-left: 0;
          border-top: 0;
          width: 4px;
          height: 9px;
          left: 4px;
          top: 0px;
          transform: rotate(45deg) scaleY(1);
          box-sizing: content-box;
          -webkit-box-sizing: content-box;
        }
      }
    }    

    &:hover {
      .checkbox-input {
        border-color: $primary-color;
      }
    }

    &.disabled {
      cursor: default;
      .checkbox-input {
        background-color: #edf2fc;
        border-color: #dcdfe6;
        &:after {
          border-color: #ccc;
        }
      }
      .label {
        color: #c0c4cc;
      }
    }

    .checkbox-original {
      opacity: 0;
      outline: none;
      position: absolute;
      margin: 0;
      width: 0;
      height: 0;
      z-index: -1;
    }
  }
}

